/* Search Bar */
.searchbox {
  display: block;
  margin: auto;margin-left: auto;
  width: 80%;
}

.relative {
  position: relative;
  width: 80%;
  margin: auto;
}

.search__input {
  width: 100%;
  margin: 0;
  padding: 0.6rem 1.5rem;
  background-color: #fff;
  color: inherit;
  outline: none;
  border-radius: 9999px;
  line-height: 1.15;
  border: 1px solid rgba(80,80,80,0.3);
}

.search__input::-webkit-input-placeholder {
  font-size: 14px;
}

.search__input:-ms-input-placeholder {
  font-size: 14px;
}

.search__input::-ms-input-placeholder {
  font-size: 14px;
}

.search__input::placeholder {
  font-size: 14px;
}

.search__input:focus {
  border: 2px solid #fbb30f;
}

/* Remove Input clear x button */
input[type="search"]::-ms-clear,
input[type="search"]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

.search__button {
  position: absolute;
  display: block;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  background: inherit;
  cursor: pointer;
  outline: none;
}

.search__button svg {
  width: 1.25rem!important;
  color: #606060;
}

.search__button svg:hover {
  color: #fbb30f;
}

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
