/*
##Device = Desktops
##Screen = 1281px to higher resolution desktops
##Device = 데스크탑
##Screen = 1281px 이상 해상도 데스크탑
*/

@media (min-width: 1821px) {


    .airlist_container {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 30px 30px;
        gap: 30px 30px;
        padding: 10px 30px;
    }

    
    .item {
        display: flex;
        max-width: 100vw;
        max-height: 100vh;
        
    }

    .livemodal {
        position: relative;
        width: 100%;
        height: 40vw;
        grid-column: span 5;
    }
}

@media (min-width: 1441px)  and (max-width: 1820px)  {


    .airlist_container {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 30px 30px;
        gap: 30px 30px;
        padding: 10px 30px;
    }

    
    .item {
        display: flex;
        max-width: 100vw;
        max-height: 100vh;
        
    }

    .livemodal {
        position: relative;
        width: 100%;
        height: 40vw;
        grid-column: span 4;
    }
}

/*
##Device = Laptops, Desktops
##Screen = B/w 1025px to 1280px
##Device = 랩탑, 데스크탑
##Screen = 1025px에서 1280px 사이
*/

@media (min-width: 1025px) and (max-width: 1440px) {


    .airlist_container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 30px 30px;
        gap: 30px 30px;
        padding: 10px 30px;
    }

   
    .item {
        display: flex;
        max-width: 100vw;
        max-height: 100vh;
        
    }

    .livemodal {
        position: relative;
        width: 100%;
        height: 40vw;
        grid-column: span 3;
    }
}


@media (min-width: 646px) and (max-width: 1024px) {


    .airlist_container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px 30px;
        gap: 30px 30px;
        padding: 10px 30px;
    }

    
    .item {
        display: flex;
        max-width: 100vw;
        max-height: 100vh;
        
    }

    .livemodal {
        position: relative;
        width: 100%;
        height: 40vw;
        grid-column: span 2;
    }

}

/*
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
##Device = 저해상도 태블릿, 모바일(가로)
##Screen = 481px에서 767px 사이
*/

@media (min-width: 481px) and (max-width: 645px) {


    .airlist_container {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 30px 30px;
        gap: 30px 30px;
        padding: 10px 30px;
    }



    .item {
        display: flex;
        max-width: 100vw;
        max-height: 100vh;
        
    }
    .livemodal {
        position: relative;
        width: 100%;
        height: 40vw;
        grid-column: span 1;
    }

}

/*
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
##Device = 대부분의 스마트폰 모바일 기기(세로)
